body {
  height: 100vh;
  background-color: #b2e0df;
  justify-content: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.search-container {
  height: 30px;
  cursor: pointer;
  background: #fff;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  padding: 10px 20px;
  transition: all .8s;
  display: flex;
  position: absolute;
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .3), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .2), inset 4px 4px 6px rgba(0, 0, 0, .2);
}

.search-container:hover > .search-input {
  width: 400px;
}

.search-container .search-input {
  width: 0;
  background: none;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 500;
  transition: all .8s;
}

.search-container .search-btn .fas {
  color: #5cbdbb;
}

.search-btn {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
}

@keyframes hoverShake {
  0% {
    transform: skew(0deg);
  }

  25% {
    transform: skew(5deg, 5deg);
  }

  75% {
    transform: skew(-5deg, -5deg);
  }

  100% {
    transform: skew(0deg);
  }
}

.search-container:hover {
  animation: hoverShake .15s linear 3;
}

.btn__load-more {
  color: #5cbdbb;
  width: 150px;
  height: 30px;
  justify-content: center;
  font-size: 20px;
  display: flex;
  position: relative;
  top: 97%;
}

